            this.CompanyName = this.GetUserInfo.CompanyName;
            this.EmailId = this.GetUserInfo.Email;


        },
        methods: {
           
            OpenChat: function () {
                var vm = this;
                vm.IsLiveChatShowHelpGuidePage = true;
                setInterval(function () {
                    window.__lc = window.__lc || {};
                    window.__lc.license = 13332330;;
                    (function (n, t, c) {
                        function i(n) {
                            return e._h ? e._h.apply(null, n) : e._q.push(n)
                        }
                        var e = {
                            _q: [],
                            _h: null,
